<script>
export default {
  data () {
    return {
      pagination: {
        page: 1,
        total: 99,
        pageTotal: 10
      }
    }
  },
  methods: {
    onChange (page) {
      this.$set(this.pagination, 'page', page)
    }
  }
}
</script>